import React,{useEffect, useState} from 'react'
import '../component/covid.css';
const Covid = () => {

    const[data,setUpdate] = useState([]);

       const getCovidData = async() =>
       {
          
            const api = await fetch("https://data.covid19india.org/data.json");
            const data = await api.json();
            console.log(data.statewise[0]);
           setUpdate(data.statewise[0]);
         
       }
    useEffect(() => {

        getCovidData();
        }, [])


    return (
        <>
            <center>
           <h1 className="abc">ðŸ”´LIVE</h1> 
           <h2 className="abc">COVID19 TRACKER BY DHANRAJ</h2>
           </center>
           <center className= "abc">    
           <div className="row">
         <div className="column">
           <div className="card1">
            <h2><span>OUR</span>&nbsp;COUNTRY</h2>
            <h1>INDIA</h1>
           
             </div>
               </div>

            <div className="column">
             <div className="card2">
             <h2><span>TOTAL</span>&nbsp;RECOVERED</h2>
             <h1>{data.recovered}</h1>
             </div>
            </div>
  
          <div className="column">
           <div className="card3">
           <h2><span>TOTAL</span> &nbsp;CONFIRMED</h2>
             <h1>{data.confirmed}</h1>
             </div>
              </div>
  
            <div className="column">
                <div className="card4">
                <h2><span>TOTAL</span>&nbsp;DEATH</h2>
                 <h1>{data.deaths}</h1>
                </div>
            </div>
            
             <div className="column">
                <div className="card5">
                <h2><span>TOTAL</span>&nbsp;ACTIVE</h2>
                 <h1>{data.active}</h1>
                </div>
            </div>

            <div className="column">
                <div className="card6">
                <h2><span>LAST</span>&nbsp;UPDATE</h2>
                 <h1>{data.lastupdatedtime}</h1> 
   
                </div>
            </div>
            </div>
            </center> 
        </>
    )
}

export default Covid
